.invitation-text {
    text-align: center;
    font-family: 'SUIT-Regular';
    font-size: 14px;
    letter-spacing: 1.4px;
    line-height: 2;
  }
  
  .invitation-text h2 {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .invitation-text p {
    margin-bottom: 0px;
  }
  
  .extra-space {
    margin-top: 60px; /* 위쪽에 20px의 간격을 추가 */
  }

  .div-margin {
    margin-bottom: 20px; /* 위쪽에 20px의 간격을 추가 */
    padding: 30px;
  }

  .background-color{
    background-color: #f9f8f3;
    box-shadow:'rgba(0, 0, 0, 0.1) 0px 0px 8px';
  }

  /* 모달 오버레이 */
.invitation-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.invitation-modal-content {
  position: relative;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  width: 220px;
  padding-bottom: 20px;
}

/* 연락처 정보 섹션 */
.contact-info {
  margin-top: 20px;
}

/* 연락처 항목을 한 줄로 배치 */
.contact-item {
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.contact-item:nth-child(1) {
  margin-bottom: 30px;
}

.contact-item:nth-child(2) {
  margin-bottom: 10px;
}

.cotant-div{
    display: flex;
    gap: 15px;
}

.contact-phone-span{
  display:flex;
  gap: 16px;
}
.contact-phone{
  font-size: 14px;
}
/* 이모티콘 배치 */
.icons {
  display: flex;
  gap: 10px;
  align-items: center;
}

/* 닫기 버튼 */
.close-button {
  position: absolute;
  top: 10px; /* 모달 상단에서의 거리 */
  right: 10px; /* 모달 오른쪽에서의 거리 */
  background-color: transparent; /* 배경 투명 */
  border: none; /* 테두리 없음 */
  font-size: 12px; /* 버튼 크기 */
  cursor: pointer; /* 클릭할 수 있도록 포인터 표시 */
}

/* 신랑, 신부 타이틀 스타일 */
.contact-title {
  font-weight: bold;
  margin-bottom: 5px;
  font-size: 14px;
  color: #333;
}

/* 이름 스타일 */
.contact-name {
  font-size: 14px;
  margin-bottom: 10px;
  color: #555;
}

/* 아이콘 간격 */
.icons {
  display: flex;
  gap: 15px;
}